import React, { Component } from 'react'
import { 
    Card, 
    Button,
    Table,
    Modal,
    message
} from 'antd'
import { PAGE_SIZE } from '../../utils/constants'
import { reqAddRole, reqRoles, reqUpdateRole } from '../../api'
import AddForm from './add-form'
import AuthForm from './auth-form'
import { formateDate } from '../../utils/dateUtils'
import { connect } from 'react-redux'
import { logout } from '../../redux/actions'

class Role extends Component {

    state = {
        roles: [],//
        role: {},//
        isShowAdd: false,//
        isShowAuth: false,//
    }

    constructor (props) {
        super(props)
        this.auth = React.createRef()
    }

    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'name'
            },
            {
                title: '',
                dataIndex: 'create_time',
                render: formateDate
            },
            {
                title: '',
                dataIndex: 'auth_time',
                render: formateDate
            },
            {
                title: '',
                dataIndex: 'auth_name'
            },
        ]
    }

    onRow = (role) => {
        return {
            onClick: event => {
                this.setState({role})
            }
        }
    }

    getRoles = async () => {
        const res = await reqRoles()
        if (res.status === 0) {
            const roles = res.data
            this.setState({roles})
        }
    }

    /**
     * 
     */
    addRole = () => {
        //
        this.form.validateFields(async (err, value) => {
            if (!err) {
                //
                const { roleName } = value
                this.form.resetFields()
                //
                const res = await reqAddRole(roleName)
                //
                if (res.status === 0) {
                    message.success('')
                    this.setState({
                        isShowAdd: false
                    })
                    this.getRoles()
                } else {
                    message.error('')
                }
            }
        })
    }

    /**
     * 
     */
    updateRole = async () => {
        const role = this.state.role
        //menus
        const menus = this.auth.current.getMenus()
        role.menus = menus
        role.auth_time = Date.now()
        role.auth_name = this.props.user.username
        //
        const res = await reqUpdateRole(role)
        if (res.status === 0) {
            if (role._id === this.props.user.role_id) {
                this.props.logout()
                message.success('')
            } else {
                message.success('')
                this.setState({
                    isShowAuth: false,
                    roles: [...this.state.roles]
                })
            }
        } else {
            message.error('')
        }
    }

    UNSAFE_componentWillMount () {
        this.initColumns()
    }

    componentDidMount () {
        this.getRoles()
    }

    render() {
        const { roles, role, isShowAdd, isShowAuth } = this.state

        const title = (
            <span>
                <Button type='primary' onClick={() => this.setState({isShowAdd: true})}></Button>&nbsp;&nbsp;
                <Button type='primary' disabled={!role._id} onClick={() => this.setState({isShowAuth: true})}></Button>
            </span>
        )

        return (
            <Card title={title}>
                <Table
                    bordered
                    rowKey='_id'
                    dataSource={roles}
                    columns={this.columns}
                    pagination={{defaultPageSize: PAGE_SIZE}}
                    rowSelection={{
                        type: 'radio', 
                        selectedRowKeys: [role._id],
                        onSelect: (role) => {//radio
                            this.setState({role})
                        }
                    }}
                    onRow={this.onRow}
                />

                <Modal
                    title=""
                    visible={isShowAdd}
                    onOk={this.addRole}
                    onCancel={() => {
                        this.setState({isShowAdd: false})
                        this.form.resetFields()
                    }}
                >
                    <AddForm
                        setForm={(form) => {this.form = form}}
                    />
                </Modal>
                <Modal
                    title=""
                    visible={isShowAuth}
                    onOk={this.updateRole}
                    onCancel={() => {
                        this.setState({isShowAuth: false})
                    }}
                >
                    <AuthForm
                        role={role}
                        ref={this.auth}
                    />
                </Modal>
            </Card>
        )
    }
}
export default connect(
    state => ({user: state.user}),
    { logout }
)(Role)