                "categoryId": "5ca9db8ab49ef916541160cb",
                "detail": "<p><span style=\"color: rgb(102,102,102);background-color: rgb(255,255,255);font-size: 16px;\">(ASUS) 6 15.6(i7-8750H 8G 256GSSD+1T GTX1050Ti 4G IPS)</span>&nbsp;</p>\n<p><span style=\"color: rgb(228,57,60);background-color: rgb(255,255,255);font-size: 12px;\">4.6-4.71T+256G</span>&nbsp;</p>\n",
