import React, {Component} from 'react'
import {withRouter} from 'react-router-dom'
import { Modal} from 'antd'

import LinkButton from '../link-button'
import {getWeather} from '../../api'
import menuList from '../../config/menuConfig'
import {formateDate} from '../../utils/dateUtils'
import pic from '../../assets/images/WeatherIcon/weather-icon-S2/256/100.png'
import './index.less'
import { connect } from 'react-redux'
import { logout } from '../../redux/actions'

/*

 */
class Header extends Component {

  state = {
    currentTime: formateDate(Date.now()), // 
    dayPictureUrl: '100', // url
    weather: '', // 
  }

  getTime = () => {
    // 1s, currentTime
    this.intervalId = setInterval(() => {
      const currentTime = formateDate(Date.now())
      this.setState({currentTime})
    }, 1000)
  }

  getWeather = async () => {
    // 
    const { now } = await getWeather()
    // 
    this.setState({
      dayPictureUrl: now.icon,
      weather: now.text
    })
  }

  getTitle = () => {
    // 
    const path = this.props.location.pathname
    let title
    menuList.forEach(item => {
      if (item.key===path) { // itemkeypath,itemtitletitle
        title = item.title
      } else if (item.children) {
        // item
        const cItem = item.children.find(cItem => path.indexOf(cItem.key)===0)
        // 
        if(cItem) {
          // title
          title = cItem.title
        }
      }
    })
    return title
  }

  /*
  
   */
  logout = () => {
    // 
    Modal.confirm({
      content: '?',
      onOk: () => {
        console.log('OK', this)
        // user
        this.props.logout()
      }
    })
  }

  /*
  render()
  : ajax/
   */
  componentDidMount () {
    // 
    this.getTime()
    // 
    this.getWeather()
  }
  /*
  // : 
  componentWillMount () {
    this.title = this.getTitle()
  }*/

  /*
  
   */
  componentWillUnmount () {
    // 
    clearInterval(this.intervalId)
  }


  render() {

    const {currentTime, weather} = this.state

    const username = this.props.user.username
    // title
    const title = this.props.headTitle //this.getTitle()
    return (
      <div className="header">
        <div className="header-top">
          <span>, {username}</span>
          <LinkButton onClick={this.logout}></LinkButton>
        </div>
        <div className="header-bottom">
          <div className="header-bottom-left">{title}</div>
          <div className="header-bottom-right">
            <span>{currentTime}</span>
            <img src={pic} alt="" />
            <span>{weather}</span>
          </div>
        </div>
      </div>
    )
  }
}

export default connect(
  state => ({
    headTitle: state.headTitle,
    user: state.user
  }),
  { logout }
)(withRouter(Header))