import React, { Component } from 'react'
import { 
    Card,
    Input,
    Icon,
    Button,
    Cascader,
    Form,
    message
} from 'antd'
import LinkButton from '../../components/link-button'
import { reqAddOrUpdateProduct, reqCategorys } from '../../api'
import PicturesWall from './pictrues-wall'
import RichTextEditor from './rich-text-editor'

const {Item} = Form
const {TextArea} = Input
/**
 * product
 */
class ProductAddUpdate extends Component {

    state = {
        options: []
    }

    constructor (props) {
        super(props)

        // ref
        this.pw = React.createRef()
        this.editor = React.createRef()
    }

    submit = () => {
        //
        this.props.form.validateFields(async (error, values) => {
            if (!error) {
                //1,product
                const { name, price, desc, categoryIds } = values
                let pCategoryId, categoryId
                if (categoryIds.length === 1) {
                    pCategoryId = '0'
                    categoryId = categoryIds[0]
                } else {
                    pCategoryId = categoryIds[0]
                    categoryId = categoryIds[1]
                }
                const imgs = this.pw.current.getImgs()
                const detail = this.editor.current.getDetail()
                const product = { name, price, desc, imgs, detail, pCategoryId, categoryId }
                //_id
                if (this.isUpdate) {
                    product._id = this.product._id
                }
                //2
                const res = await reqAddOrUpdateProduct(product)
                //3
                if (res.status === 0) {
                    message.success(`${this.isUpdate ? '' : ''}!`)
                    this.props.history.goBack()
                } else {
                    message.error(`${this.isUpdate ? '' : ''}!`)
                }
            }
        })
    }

    /**
     * 
     * @param {*} rule 
     * @param {*} value 
     * @param {*} callback 
     */
    validatePrice = ( rule, value, callback) => {
        if (value*1 > 0) {
            callback()//
        } else {
            callback('0')//
        }
    }

    initOptions = async categorys => {
        //categorysoptions
        const options = categorys.map(c => ({
            value: c._id,
            label: c.name,
            isLeaf: false
        }))

        //
        const { isUpdate, product } = this
        const { pCategoryId } = product
        if (isUpdate && pCategoryId !== '0') {
            //
            const subCategorys = await this.getCategorys(pCategoryId)
            //options
            const childOptions = subCategorys.map(c => ({
                value: c._id,
                label: c.name,
                isLeaf: true
            }))
            //option
            const targetOption = options.find(option => option.value === pCategoryId)
            // option
            targetOption.children = childOptions
        }
        //options
        this.setState({options})
    }

    /**
     * 
     * asyncpromisepromiseasync
     * @param {number} parentId 
     */
    getCategorys = async (parentId) => {
        const res = await reqCategorys(parentId)
        if (res.status === 0) {
            const categorys = res.data
            if (parentId === '0') {
                //
                this.initOptions(categorys)
            } else {
                //
                return categorys//asyncpromisecategorys
            }
        }
    }

    /** */
    loadData = async selectedOptions => {
        // option
        const targetOption = selectedOptions[0]
        // loading
        targetOption.loading = true
    
        // , 
        const subCategorys = await this.getCategorys(targetOption.value)
        // loading
        targetOption.loading = false
        // 
        if (subCategorys && subCategorys.length>0) {
          // options
          const childOptions = subCategorys.map(c => ({
            value: c._id,
            label: c.name,
            isLeaf: true
          }))
          // option
          targetOption.children = childOptions
        } else { // 
          targetOption.isLeaf = true
        }
    
        //options
        this.setState({
          options: [...this.state.options],
        })
    }
    
    componentDidMount () {
        this.getCategorys('0')
    }

    UNSAFE_componentWillMount () {
        //state
        const product = this.props.location.state//
        //
        this.isUpdate = !!product
        // (, {})
        this.product = product || {}
    }

    render() {
        const {isUpdate, product} = this
        const {pCategoryId, categoryId, imgs, detail} = product

        //ID
        const categoryIds = []
        if (isUpdate) {
            //
            if (pCategoryId === '0') {
                categoryIds.push(categoryId)
            } else {
                //
                categoryIds.push(pCategoryId)
                categoryIds.push(categoryId)
            }
            
        }

        const title = (
            <span>
                <LinkButton onClick={() => this.props.history.goBack()}>
                    <Icon type='arrow-left'></Icon>
                </LinkButton>
                <span>{isUpdate ? '' : ''}</span>
            </span>
        )

        // Item
        const formItemLayout = {
            labelCol: {span: 2},//label
            wrapperCol: {span: 8}//
        }

        const {getFieldDecorator} = this.props.form

        return (
           <Card title={title}>
               <Form {...formItemLayout}>
                    <Item label=''>
                        {
                            getFieldDecorator('name', {
                                initialValue: product.name,
                                rules: [
                                    {required: true, message: ''}
                                ]
                            })(
                                <Input placeholder=''></Input>
                            )
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('desc', {
                                initialValue: product.desc,
                                rules: [
                                    {required: true, message: ''}
                                ]
                            })(
                                <TextArea placeholder='' autoSize={{minRows: 2, maxRows: 6}}></TextArea>
                            )
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('price', {
                                initialValue: product.price,
                                rules: [
                                    {required: true, message: ''},
                                    {validator: this.validatePrice}
                                ]
                            })(
                                <Input placeholder='' type='number' addonAfter=''></Input>
                            )
                        }
                    </Item>
                    <Item label=''>
                        {
                            getFieldDecorator('categoryIds', {
                                initialValue: categoryIds,
                                rules: [
                                    {required: true, message: ''}
                                ]
                            })(
                                <Cascader 
                                    placeholder=''
                                    options={this.state.options}//
                                    loadData={this.loadData}//
                                />
                            )
                        }
                    </Item>
                    <Item label=''>
                        <PicturesWall ref={this.pw} imgs={imgs}/>
                    </Item>
                    <Item label='' labelCol={{span: 2}} wrapperCol={{span: 20}}>
                        <RichTextEditor ref={this.editor} detail={detail}></RichTextEditor>
                    </Item>
                    <Item>
                        <Button type='primary' onClick={this.submit}></Button>
                    </Item>
               </Form>
           </Card>
        )
    }
}
export default Form.create()(ProductAddUpdate)
/*
1. : , 
2. : ref(), 
 */

/*
ref
1. ref: thi.pw = React.createRef()
2. ref: <PictureWall ref={this.pw} />
3. ref: this.pw.current
 */