import React, { Component } from 'react'
import { Chart, Geom, Axis, Tooltip } from 'bizcharts'

export default class Bar extends Component {
    render() {
        const data = [
            {
              year: "1",
              sales: 38
            },
            {
              year: "2",
              sales: 52
            },
            {
              year: "3",
              sales: 61
            },
            {
              year: "4",
              sales: 145
            },
            {
              year: "5",
              sales: 48
            },
            {
              year: "6",
              sales: 38
            },
            {
              year: "7",
              sales: 28
            },
            {
              year: "8",
              sales: 38
            },
            {
              year: "59",
              sales: 68
            },
            {
              year: "10",
              sales: 38
            },
            {
              year: "11",
              sales: 58
            },
            {
              year: "12",
              sales: 38
            }
        ]
        const cols = {
            sales: {
                tickInterval: 20
            }
        }
        return (
            <div style={{width: '100%', marginLeft: -30}}>
                <Chart height={338} data={data} scale={cols} forceFit>
                    <Axis name='year'></Axis>
                    <Axis name='sales'></Axis>
                    <Tooltip crosshairs={{type: 'y'}} />
                    <Geom type='interval' position='year*sales'></Geom>
                </Chart>
            </div>
        )
    }
}
