import React, { Component } from 'react'
import { 
    Card,
    Button,
    Table,
    Modal,
    message
} from 'antd'
import { formateDate } from '../../utils/dateUtil'
import LinkButton from '../../components/link-button'
import { PAGE_SIZE } from '../../utils/constants'
import { reqAddOrUpdateUser, reqDeleteUser, reqUsers } from '../../api'
import UserForm from './user-form'

export default class User extends Component {

    state = {
        users: [], //
        roles: [], //
        isShow: false,//
        loading: false
    }

    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'username'
            },
            {
                title: '',
                dataIndex: 'email'
            }, 
            {
                title: '',
                dataIndex: 'phone'
            },
            {
                title: '',
                dataIndex: 'create_time',
                render: formateDate
            },
            {
                title: '',
                dataIndex: 'role_id',
                render: id => this.roleNames[id]
            },
            {
                title: '',
                render: (user) => (
                    <span>
                        <LinkButton onClick={() => this.showUpdate(user)}></LinkButton>&nbsp;&nbsp;
                        <LinkButton onClick={() => this.deleteUser(user)}></LinkButton>
                    </span>
                )
            }
        ]
    }

    /**
     * 
     * @param {any} user 
     */
    showUpdate = (user) => {
        this.user = user//user
        this.setState({
            isShow: true
        })
    } 

    /**
     * 
     */
    addOrUpdateUser = () => {
        //1
        this.form.validateFields( async (err, value) => {
            if (!err) {
                this.setState({isShow: false})
                this.form.resetFields()
                let isUser = false
                if (this.user && this.user._id) {
                    isUser = true
                    value._id = this.user._id
                } 
                //2
                const res = await reqAddOrUpdateUser(value)
                //3
                if (res.status === 0) {
                    message.success(`${isUser ? '' : ''}!`)
                    this.getUsers()
                } else {
                    message.error(`${isUser ? '' : ''}!`)
                }
            }
        })
    }

    /**
     * 
     */
    getUsers = async () => {
        this.setState({
            loading: true
        })
        const res = await reqUsers()
        if (res.status === 0) {
            const { users, roles } = res.data
            this.initRoleNames(roles)
            this.setState({
                users,
                roles,
                loading: false
            })
        }
    }

    deleteUser = (user) => {
        Modal.confirm({
            title: `${user.username}`,
            onOk: async () => {
                const res = await reqDeleteUser(user._id)
                if (res.status === 0) {
                    message.success('')
                    this.getUsers()
                } else {
                    message.error('')
                }
            }   
        })

    }

    /**
     * roleid
     * @param {any[]} roles 
     */
    initRoleNames = (roles) => {
        this.roleNames = roles.reduce((pre, role)=>{
            pre[role._id] = role.name
            return pre
        }, {})
    }

    componentWillMount () {
        this.initColumns()
    }

    componentDidMount () {
        this.getUsers()
    }

    render() {

        const { users, isShow, loading, roles } = this.state
        const user = this.user || {}
        const title = <Button type='primary' onClick={() => this.setState({isShow: true})}></Button>

        return (
            <Card title={title}>
                <Table 
                    bordered
                    rowKey='_id'
                    loading={loading}
                    dataSource={users}
                    columns={this.columns}
                    pagination={{defaultPageSize: PAGE_SIZE, showQuickJumper: true}}
                />
                <Modal
                    title={`${user._id ? '' : ''}`}
                    visible={isShow}
                    onOk={this.addOrUpdateUser}
                    onCancel={() => {
                        this.setState({isShow: false}, () => {
                            this.user = {}
                        })
                        this.form.resetFields()
                    }}
                >
                    <UserForm 
                        setForm={form => this.form = form}
                        roles={roles}
                        user={user}
                    ></UserForm>
                </Modal>
            </Card>
        )
    }
}
