import { combineReducers } from "redux"
import storageUtils from "../utils/storageUtils"
import { RECEIVE_USER, RESET_USER, SET_HEAD_TITLE, SHOW_ERROR_MSG } from "./action-types"

const initTitle = ''
const initUser = storageUtils.getUser()

/**
 * 
 * @param {any} state 
 * @param {any} action 
 */
function user (state = initUser, action) {
    switch (action.type) {
        case RECEIVE_USER:
            return action.user
        case SHOW_ERROR_MSG:
            const err = action.err
            return {...state, err}
        case RESET_USER:
            return {}
        default:
            return state
    }
}

/**
 * 
 * @param {any} state 
 * @param {any} action 
 */
function headTitle (state = initTitle, action) {
    switch (action.type) {
        case SET_HEAD_TITLE:
            return action.data
        default:
            return state
    }
}

/**
 * reducer
 * state
 * {
 *      headTitle: '',
 *      user: {}
 * }
 */
export default combineReducers({
    user,
    headTitle
})
